import Vue from 'vue';
import Router from "vue-router";

Vue.use(Router)

const routes = [
  {
    path: '/',
    name: 'login',
    component: () => import('../views/login.vue'),
  },
  {
    path: '/sisge',
    component: () => import('../views/Home.vue'),
    children: [
      //dashboard
      {
        path: '',
        name: 'dashboard',
        component: () => import('../views/Dashboard.vue'),
      },
      //usuario
      {
        path: 'usuario-perfil',
        name: 'usuario-perfil',
        component: () => import('../views/Perfil.vue'),
      },
      //expedientes
      {
        path: 'expediente/registro',
        name: 'expediente/registro',
        component: () => import('../views/Expediente/Registro.vue'),
      },
      {
        path: 'expediente/busqueda',
        name: 'expediente/busqueda',
        component: () => import('../views/Expediente/Buscar.vue'),
      },
      {
        path: 'expediente/lista',
        name: 'expediente/lista',
        component: () => import('../views/Expediente/Lista.vue'),
        props: true,
      },
      {
        path: 'expediente/detalle/:id',
        name: 'expediente/detalle',
        component: () => import('../views/Expediente/Detalle.vue'),
        props: true,
      },
      {
        path: 'expediente/actualizacion/:id',
        name: 'expediente/actualizacion',
        component: () => import('../views/Expediente/Mantenimiento/Update.vue'),
        props: true,
      },
      //archivos - eventos
      {
        path: 'archivos/:id',
        name: 'archivos',
        component: () => import('../views/Expediente/Mantenimiento/Archivos.vue')
      },

      {
        path: 'eventos/:id',
        name: 'eventos',
        component: () => import('../views/Expediente/Mantenimiento/Eventos.vue')
      },

      //colaboradores
      {
        path: 'abogados',
        name: 'abogados',
        component: () => import('../views/Colaboradores/Abogado.vue')
      },

      //partes procesales
      {
        path: 'demandantes',
        name: 'demandantes',
        component: () => import('../views/Procesales/Demandante.vue')
      },
      {
        path: 'demandante/informacion/:doc',
        name: 'demandante/informacion',
        component: () => import('../views/Procesales/Demandante/Informacion.vue'),
        props: true,
      },
      {
        path: 'demandados',
        name: 'demandados',
        component: () => import('../views/Procesales/Demandado.vue')
      },
      {
        path: 'demandado/informacion/:doc',
        name: 'demandado/informacion',
        component: () => import('../views/Procesales/Demandado/Informacion.vue'),
        props: true,
      },
      //actividades
      {
        path: 'calendario',
        name: 'calendario',
        component: () => import('../views/Actividades/Calendario.vue')
      },

      {
        path: 'audiencias',
        name: 'audiencias',
        component: () => import('../views/Actividades/Audiencias.vue')
      },
      {
        path: 'alertas',
        name: 'alertas',
        component: () => import('../views/Actividades/Alertas.vue')
      },

      //datos
      {
        path: 'reportes',
        name: 'reportes',
        component: () => import('../views/Datos/Reportes.vue')
      },
      //procesos
      {
        path: 'historial/registro-demandante/:doc',
        name: 'historial/registro-demandante',
        component: () => import('../views/Historial/registroDemandante.vue')
      },
      {
        path: 'historial/registro-demandado/:doc',
        name: 'historial/registro-demandado',
        component: () => import('../views/Historial/registroDemandado.vue')
      },
      // -- matenimientos
      {
        path: 'mantenimiento/cambio-abogado',
        name: 'mantenimiento/cambio-abogado',
        component: () => import('../views/Mantenimiento/cambio_abogado.vue')
      },
      {
        path: 'mantenimiento/distritos-judiciales',
        name: 'mantenimiento/distritos-judiciales',
        component: () => import('../views/Mantenimiento/distrito_judicial.vue')
      },
      {
        path: 'mantenimiento/instancia',
        name: 'mantenimiento/instancia',
        component: () => import('../views/Mantenimiento/instancia.vue')
      },
      {
        path: 'mantenimiento/especialidad',
        name: 'mantenimiento/especialidad',
        component: () => import('../views/Mantenimiento/especialidad.vue')
      },
      {
        path: 'mantenimiento/materias',
        name: 'mantenimiento/materias',
        component: () => import('../views/Mantenimiento/materias.vue')
      },
      {
        path: 'mantenimiento/pretension',
        name: 'mantenimiento/pretension',
        component: () => import('../views/Mantenimiento/pretension.vue')
      },
      {
        path: 'mantenimiento/organo-jurisdiccional',
        name: 'mantenimiento/organo-jurisdiccional',
        component: () => import('../views/Mantenimiento/juzgado.vue')
      },
     

    ]
  },

]
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  mode: 'hash',
  linkActiveClass: "open active",
  scrollBehavior: () => ({ y: 0 }),
  routes
})
