<template>
  <div id="app">
    <router-view />
    <Loading v-show="loading"></Loading>
  </div>
</template>
<script>
import Loading from './components/Loader/LoaderModal.vue'

import { bus } from './main'

export default {
  name: 'app',
  components: {
    Loading
  },
  data () {
    return {
      loading: false
    }
  },
  created () {
    bus.$on('loading', (data) => {
      this.loading = data
    })

    const token = this.$store.getters.get__token;
  const role = this.$store.getters.get__user.usu_rol;
  
    // if (this.$store.getters.get__token) {
    //   this.$router.push('/sisge')
    // } else {
    //   this.$router.push('/')
    // }
    const storedRoute = localStorage.getItem('currentRoute');
    if (storedRoute && this.$store.getters.get__token) {
      this.$router.push(storedRoute);
    } else {
      this.$router.push('/');
    }

  }
}
</script>

